<template>
    <div class="bill">
        <div class="field-part">
            <cell title="信用卡卡号" @click.native="showCreditAccount()" v-model="creditData.account" isLink select></cell>
            <selector-payment-account ref="creditAccount" :options=" creditAccount "></selector-payment-account>
        </div>
        <div class="field-part">
            <cell title="借记卡卡号" @click.native="showDebitAccount()" v-model="debitData.account" isLink select></cell>
            <mt-field ref="amount" label="还款金额" @change.native="amountFocus()" placeholder="请输入还款金额" v-model="debitData.amount"></mt-field>
            <selector-payment-account ref="debitAccount" :options=" debitAccount "></selector-payment-account>
        </div>
    </div>
</template>

<script>
    import * as service from '@/services/payment';
    export default {
        name: 'bill',
        data() {
            return {
                creditData: {
                    account: ''
                },
                debitData: {
                    account: '',
                    amount: ''
                },
                creditAccount: {
                    title: '选择信用卡',
                    type: 'credit',
                    checked: {},
                    showAddBtn: false,
                    showWarn: false,
                    accountList: []
                },
                debitAccount: {
                    title: '选择借记卡',
                    checked: {},
                    showAddBtn: false,
                    showWarn: false,
                    accountList: []
                }
            };
        },
        created() {
            service.getAccountList()
                .then((res) => {
                    this.creditAccount.accountList = res.data;
                    this.debitAccount.accountList = res.data;
                }, (err) => {
                    console.log(err);
                });
        },
        methods: {
            showCreditAccount() {
                this.$refs.creditAccount.show().then((data) => {
                    this.creditAccount.checked = data.item;
                    this.creditData.account = this.creditAccount.checked.account;
                }, (data) => {
                });
            },
            showDebitAccount() {
                this.$refs.debitAccount.show().then((data) => {
                    this.debitAccount.checked = data.item;
                    this.debitData.account = this.debitAccount.checked.account;
                }, (data) => {
                });
            },
            amountFocus() {
                console.log('666');
            }
        }
    };

</script>

<style lang="scss" scoped>
    .field-top {
        position: relative;
        display: flex;
        display: -webkit-box;
        display: -ms-flexbox;
        padding: 20px;
        background-color: #fafafa;
        @include mini-border(top);

        .left {
            flex: 1;
        }
    }

    .field-cell {
        position: relative;
        padding: 20px;
        margin-bottom: 15px;
        color: #26a2ff;
        background-color: #fff;
        text-align: center;
        @include mini-border(top, #999, after);
        @include mini-border(bottom, #999, before);
    }
</style>


